import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AddEditService } from './add-edit.service';
import { Vendor } from '../vendor/vendor';
import { AuthService } from '../guards/auth.service';

@Component({
  selector: 'app-add-edit',
  templateUrl: './add-edit.component.html',
  styleUrls: ['./add-edit.component.scss']
})
export class AddEditComponent implements OnInit {
  vendors: Vendor[];
  vendor = new Vendor();
  constructor(private router:Router,private auth:AuthService,private route: ActivatedRoute,private addedit : AddEditService) { }
  vid : any;
  ngOnInit() {
    this.vid=this.route.snapshot.paramMap.get('id')
    if(this.vid)
    {
      this.getVendorById();
    }
    else{
      this.reset();
    }
  }
  getVendorById(){
    
    this.addedit.getVenbyId(this.vid)
    .subscribe((venbyId)=>{
      this.vendor=venbyId;
      console.log(venbyId);
    },(error)=>{
      console.log(error);
    });
  }
  
  private reset(){
    this.vendor.vendorId=null;
    this.vendor.vendorName=null;
    this.vendor.vendorAdd=null;
    this.vendor.vendorLoc=null;
    this.vendor.vendorSer=null; 
    this.vendor.vendorPin=null;
    this.vendor.isActive=null;
    this.vendor.staffId=null;
    this.vendor.staffName=null;
    this.vendor.staffDept=null; 
    this.vendor.staffEmail=null;
    this.vendor.satffPhno=null;

  }
  add_edit_vendor(){
    this.addedit.addVendor(this.vendor).subscribe((response)=>{console.log(response); 
      this.back();
  },(error)=>{console.log(error)});
}

back(){
  this.router.navigate(['/vendor']);

}
logout(){
this.auth.logout();
}
}
