import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from './vendor.service';
import { Vendor } from './vendor';
import { AuthService } from '../guards/auth.service';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {

  constructor(private router: Router, private service: VendorService, private route: ActivatedRoute,private auth:AuthService) { }
  vendors: Vendor[];
  vendor = new Vendor();
  searchText;
  searchid: any;
  ngOnInit() {
    this.getVendors();
  }
  add_vendor() {
    this.router.navigate(['add_vendor'])
  }
  edit_vendor(data: any) {
    this.router.navigate(['edit_vendor', data]);
  }
    view_vendor(data:any){
      this.router.navigate(['view_vendor', data]);
    }
    logout(){
      this.auth.logout();
    }
  
  getVendors() {
    this.service.getAllVendors()
      .subscribe((staffData) => {
        this.vendors = staffData;
        console.log(staffData);
      }, (error) => {
        console.log(error);
      });
  }
  disable() {
    console.log("inside disable" + this.searchid);
    this.service.disableVendor(this.searchid)
      .subscribe((vendorData) => {
        console.log(vendorData);
        this.getVendors();
      }, (error) => {
        console.log(error);
      });
  }
  onSelect(selectedVendor: any) {
    console.log("Selected item Id:", selectedVendor.vendorId);
    this.searchid = selectedVendor.vendorId;
  }
  
}
