import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ViewstaffService } from './viewstaff.service';
import { Vendor } from '../vendor/vendor';
import { AuthService } from '../guards/auth.service';

@Component({
  selector: 'app-viewstaff',
  templateUrl: './viewstaff.component.html',
  styleUrls: ['./viewstaff.component.scss']
})
export class ViewstaffComponent implements OnInit {
  vId:any;
  vendor = new Vendor();

  constructor(private router: Router, private service: ViewstaffService,private auth:AuthService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.getVendorById();
    this.resetform();
  }
  logout(){
    this.auth.logout();
  }
  resetform() {
    this.service.vendor = {
      vendorId: null,
      vendorName: '',
      vendorAdd: '',
      vendorLoc: '',
      vendorSer: '',
      vendorPin: '',
      staffId: null,
      staffName: '',
      staffDept: '',
      staffEmail: '',
      satffPhno: '',
      isActive: null
    }
  }
  

  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this.service.getVendorId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      console.log(this.vendor.vendorName);
    },(error)=>{
      console.log(error);
    })
  }

}
