import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Vendor } from '../vendor/vendor';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Login } from '../login/login';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  formData: Login;

  constructor(private router: Router, private _httpService: HttpClient) { }

  getRole(formData: any): any {
    return this._httpService.get<Login>(environment.APIUrl + '/user/' + formData.username + '/' + formData.password);

  }

  logout() :void 
  {    
    localStorage.setItem('isLoggedIn','false');    
    localStorage.removeItem('token');  
    localStorage.removeItem('tokenRoleId');
    this.router.navigate(['/login']);   
  } 

}